<template>
  <div class="user-card">
    <el-dropdown>
      <div class="el-dropdown-link">
        <img class="avatar" :src="defaultAvatar" alt="头像">
      </div>
      <el-dropdown-menu slot="dropdown" class="detail">
        <el-dropdown-item>
          <router-link to="/home">用户信息</router-link>
        </el-dropdown-item>
        <el-dropdown-item>
          <div @click="openDrawer">布局配置</div>
        </el-dropdown-item>
        <el-dropdown-item>
          <div @click="drawer = true">权限切换</div>
        </el-dropdown-item>
        <el-dropdown-item>
          <a target="_blank" href="https://github.com/TaleCeres/ceres-admin">Github地址</a>
        </el-dropdown-item>
        <el-dropdown-item divided icon="el-icon-switch-button" @click.native="handleLogout">
          注销
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
    <el-drawer
      :visible.sync="drawer"
      direction="rtl">
      <el-select v-model="auth" placeholder="请选择" @change="changeAuth">
        <el-option
          v-for="item in auths"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-drawer>
  </div>
</template>

<script type="text/ecmascript-6">
import { mapMutations, mapActions } from 'vuex'
import defaultAvatar from 'assets/images/company/logo.png'
export default {
  name: 'UserCard',
  components: {},
  data() {
    return {
      drawer: false,
      auths: [
        {
          label: '超级管理员',
          value: 1
        },
        {
          label: '管理员',
          value: 2
        },
        {
          label: '普通用户',
          value: 14
        }
      ],
      auth: this.$store.state.user.user.auth,
      defaultAvatar,
    }
  },
  computed: {},
  created() { },
  mounted() {
  },
  methods: {
    ...mapActions({
      logout: 'user/logout',
    }),
    ...mapMutations({
      openDrawer: 'app/OPEN_DRAWER'
    }),
    handleLogout() {
      this.logout()
      window.location.reload(true)
    },
    async changeAuth(val) {
      const map = {
        1: {
          account: '462870781@qq.com',
          secret: '123456',
          type: 101
        },
        2: {
          account: '462870782@qq.com',
          secret: '123456',
          type: 101
        },
        14: {
          account: '12312321@111.com',
          secret: '123456',
          type: 101
        },
      }
      await this.$model.inject(this).user.getToken(map[val].account, map[val].secret)
      window.location.reload()
    }
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.user-card {
  .el-dropdown-link {
    .avatar {
      width 37px
      height 37px
      border-radius 50%
    }
  }
  .detail {
    width 326px
    margin-bottom 0
    padding-bottom 0
    border 1px solid blue
  }
}
</style>
